
export interface Errors{
    name: string
    cellPhone: string
    email: string
    telephone:string
    treatment: string
}

const AppointmentFormValidator = (values:any) => {
    let errors:Errors = {
        name:'',
        cellPhone: '',
        email: '',
        treatment: '',
        telephone: ''
    };
    if (!values.name) {
        errors.name = 'Nombre completo requerido';
    } else if (!/^[a-zA-Z]+$/.test(values.name)) {
        errors.name = 'Nombre invlido';
    }
    
    if (!values.cellPhone) {
        errors.cellPhone = 'Nmero de celular requerido';
    } else if (!/^[0-9]*$/.test(values.cellPhone)) {
        errors.cellPhone = 'Nmero de Celular invlido';
    }

    if (!values.email) {
        errors.email = 'Email requerido';
    } else if (!/\S+@\S+\.\S+/.test(values.email)) {
        errors.email = 'Email invlido';
    }

    if (!values.telephone) {
        errors.telephone = 'Telfono requerido';
    } else if (!/^[0-9]*$/.test(values.telephone)) {
        errors.telephone = 'Telfono invlido';
    }

    if (!values.treatment) {
        errors.treatment = 'Elija un tratamiento';
    } 
    
    return errors;
}

export default AppointmentFormValidator